@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&di);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgba(0, 0, 0, 0.85);
}


.app {
  background-image: url(/WeatherApp/static/media/mountain-default.91e7d8b9.jpg);
  background-size: cover;
  width: 100%;
  max-width: 70rem;
  margin: 3rem auto;
  height: 90vh;
  border-radius: 10px;
  font-family: 'Open Sans', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.SearchBar_form__3-Lq7 {
    display: flex;
    justify-content: center;
    
}

.SearchBar_form__3-Lq7 input{
    width: 100%;
    max-width: 30rem;
    padding: 1rem;

    outline: none;
    border: none;
    
    background-color: rgba(255, 255, 255, 0.55);
    border-radius: 0px 0px 0 1rem;

    font-size: 1rem;
    font-family: inherit;

    transition: 0.4s ease;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.1);
}

.SearchBar_form__3-Lq7 input:focus {
    background-color: rgba(255, 255, 255, 0.75);
}

.SearchBar_form__3-Lq7 button{
    font-family: inherit;
    font-size: 1rem;
    padding: 1rem;
   

    outline: none;
    border: none;
    border-radius: 0 0 1rem 0;

    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.55);
    color: #FFF;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
}
.LocationAndDate_location__2USEJ {
