}

.LocationAndDate_date__2-uIG {
    font-size: 1.5rem;
    text-align: center;
    text-shadow: 2px 2px rgba(0, 0, 0, 0.4);
    color: rgb(243, 239, 239);
    font-style: italic;
}
.Weather_weather__1y497 {
